.flex-item-menu {
    width: 80px;
    min-width: 80px;
    border-radius: 4px;
    display: flex;
    /*flex: 1;*/
   /* background-color: antiquewhite; */
    transition: all 300ms ease-in-out;
    margin-left: -80px;
}

.flex-item-menu.expanded {
    margin-left: 0;
    min-width: 200px;
    width: 200px;
}

.menu-container {
    width: 100%;
   /* list-style-type: none; */   
   
}

.menu-list a:hover {
    text-decoration: none;    
}

.menu {
    text-align: right;
}

.menu.expanded {
    text-align: left;
    display: block;
}
